@media (prefers-color-scheme: dark) {
  :root {
    color-scheme: dark light;
  }
}

body {
  margin: 0;
  font-family: sans-serif;
}

#cy {
  width: 100%;
  height: 100%;
  position: relative;
  left: 0;
  top: 0;
}

#add-node {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.1rem;
}
